import React from "react";
import axios from "axios";
import Movie from "../components/Movie";
import "./Home.css";

class Home extends React.Component {
  state = {
    isLoading: true,
    movies: [],
  };
  getMovies = async () => {
    const {
      data: {
        data: { movies },
      },
    } = await axios.get("https://yts-proxy.now.sh/list_movies.json?sort_by=rating");
    this.setState({
      movies,
      isLoading: false,
    }); //data:axios
  };
  componentDidMount() {
    this.getMovies();
  }
  render() {
    const { isLoading, movies } = this.state;
    return (
      <section className="container">
        {isLoading ? (
          <div class="loader">
            <span class="loader__text"> Loading... </span>
          </div>
        ) : (
          <div class="movies">
            {movies.map((movie) => (
              <Movie key={movie.id} id={movie.id} year={movie.year} title={movie.title} summary={movie.summary} poster={movie.medium_cover_image} genres={movie.genres} />
            ))}
          </div>
        )}
      </section>
    );
  }
}
//this.add()는 결과를 넣어주는 것, ()가 없을경우는 함수 자체를 넣어준 것
export default Home;

//setState를 호출하면 react는 다시 render함
